.coupons {
  margin: 78px 0 140px;
  h3 {
    margin-bottom: 52px;
    font-size: 22px;
    color: #1f1f1f;
  }
  .notice {
    margin-bottom: 40px;
    li {
      color: #898989;
      font-size: 14px;
      position: relative;
      margin-bottom: 18px;
      padding-left: 8px;
      &::before {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        display: block;
        width: 2px;
        height: 2px;
        background-color: #c1c1c1;
      }
    }
  }
  .tab {
    padding-bottom: 50px;
  }
  .coupon-list {
    margin-left: -64px;
    .list {
      float: left;
      width: 454px;
      height: 410px;
      margin-left: 64px;
    }
    .coupon {
      color: #1f1f1f;
      text-align: left;
      position: relative;
      box-sizing: border-box;
      display: block;
      width: 100%;
      margin-bottom: 20px;
      padding: 30px 99px 30px 30px;
      border: 1px solid #f1f1f1;
      .label {
        color: #fff;
        font-size: 20px;
        line-height: 1.4;
        text-align: center;
        vertical-align: middle;
        position: absolute;
        top: -1px;
        right: -1px;
        bottom: -1px;
        box-sizing: border-box;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 100px;
        padding: 0 25px;
        background-color: #f13d4e;
      }
      .brand {
        font-size: 14px;
        font-weight: bold;
      }
      .title {
        margin-top: 8px;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.4;
        word-break: keep-all;
        max-width: 230px;
        height: 90px;
      }
      .sale {
        font-size: 30px;
        font-weight: bold;
      }
      .note {
        color: #898989;
        text-align: right;
        position: absolute;
        right: 129px;
        bottom: 30px;
        font-size: 13px;
        span {
          display: block;
        }
      }
    }
    ul {
      color: #898989;
      font-size: 14px;
      line-height: 1.8571;
      margin-left: 10px;
      em {
        color: #1f1f1f;
      }
      a {
        color: #1f1f1f;
        text-decoration: underline;
      }
    }
    &::after {
      content: '';
      display: block;
      clear: both;
    }
  }
  .no-result {
    color: #898989;
    font-size: 16px;
    line-height: .15;
    text-align: center;
    margin: 160px 0;
  }
}
.layer-offline-coupon {
  .layer {
    .inner {
      .content {
        ul.list-dot-style {
          margin-bottom: 20px;
          li {
            color: #898989;
            font-size: 14px;
            line-height: 1;
            &::before {
              top: 6px;
              background-color: #898989;
            }
          }
        }
        .select-shop {
          float: left;
          width: 398px;
          select, input[type="text"] {
            display: inline-block;
            width: 386px;
            height: 35px;
            margin-right: 10px;
            margin-bottom: 8px;
            padding: 0 10px;
            border: 1px solid #c6c6c6;
            box-sizing: border-box;
          }
          select {
            width: 186px;
            &:invalid {
              color: #898989;
            }
          }
        }
        .btn {
          float: left;
          width: 78px;
          height: 78px;
          margin-bottom: 8px;
        }
        .shop-list {
          float: left;
          width: 476px;
          height: 270px;
          margin-top: 20px;
          padding-top: 13pxpx;
          background-color: #f7f7f7;
          overflow-y: scroll;
          li {
            float: left;
            width: 198px;
            padding-left: 30px;
            color: #898989;
            font-size: 13px;
            line-height: 35px;
          }
          &::-webkit-scrollbar {
            width: 4px;
            height: 4px;
            background-color: #f7f7f7;
          }
          &::-webkit-scrollbar-thumb {
            position: relative;
            background: #666;
          }
        }
      }
    }
  }
}